:root {
  --theme: #ea596e;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Varela Round, sans-serif;
}

core-navbar {
  background: var(--theme);
  color: #fff;
  justify-content: space-between;
  font-size: x-large;
  display: flex;
}

core-navbar a {
  color: #fff;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
}

core-navbar a:hover, core-navbar a:active {
  transform: scale(1.4);
}

core-navbar > div {
  padding: 8px;
}

.title-color-brain, .title-color-brain-hover:hover {
  color: #f4abba;
}

.title-color-brain-outline, .title-color-brain-outline-hover:hover {
  color: #ea596e;
}

.title-color-text-outline, .title-color-text-outline-hover:hover {
  color: #d82d46;
}

.games-button {
  cursor: pointer;
  color: #595959;
  text-align: center;
  z-index: 2;
  width: 20%;
  height: 35%;
  background-color: #fff;
  border: 2px solid #707070;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 28px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
}

.games-button:hover {
  color: #fff;
  background-color: #ea596e;
  transition: all .3s;
}

.game-button-img {
  width: 60%;
}

.title-img {
  width: 35%;
  margin-top: 5%;
  margin-left: auto;
  margin-right: auto;
  transition: all .6s;
  display: block;
}

.game-details {
  opacity: 0;
  transition: all .6s;
  position: absolute;
  top: 15%;
  left: 10%;
}

.game-details-title {
  width: 40%;
  color: #595959;
  font-size: 28px;
  font-weight: bold;
}

.game-details-text {
  width: 40%;
  color: #595959;
  font-size: 22px;
}

.game-details-img {
  width: 30%;
  position: absolute;
  top: 5%;
  left: 60%;
}

#capidle-btn {
  position: absolute;
  bottom: 5%;
  left: 10%;
}

#liedle-btn {
  position: absolute;
  bottom: 5%;
  left: 40%;
}

#a2048expert-btn {
  position: absolute;
  bottom: 5%;
  left: 70%;
}

#modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #0006;
  animation: 1s forwards fade;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

@keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.modal-window {
  max-width: 50%;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #fff;
  border-radius: 8px;
  margin: 80px auto 0;
  padding: 24px;
  position: absolute;
  left: 0;
  right: 0;
  box-shadow: 0 10px 20px #0003, 0 6px 6px #0003;
}

@media only screen and (max-width: 700px) {
  .modal-window {
    max-width: 75%;
  }
}

.modal-header {
  text-align: center;
  margin-bottom: 36px;
  font-size: 24px;
}

.modal-text {
  margin-bottom: 32px;
  margin-left: 16px;
  font-size: 16px;
  line-height: 20px;
}

#close-btn {
  color: #787c7e;
  float: right;
  margin-top: -16px;
  font-size: 32px;
}

#close-btn:hover, #close-btn:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

